/**
 * @author zhaohuai
 * id
 * 
 */
export const getCompanyInfoByGroupId = (groupId) => request('/H_roleplay-si/ds/getGroupById',{
    groupId:groupId
})
/**
 * @author zhaohuai
 * 
 */
export const saveCompanyInfo = (data) =>{
    let {
        groupName,
        groupShortName,
        depFunction,
        status,
        comment,
        groupCode
    } = data
    return request('/H_roleplay-si/userInfo/saveGroup',{
        groupName: groupName,
        groupShortName: groupShortName,
        depFunction: depFunction,
        status: status,
        comment: comment,
        list: 'sys_group',
        groupType: 'C',
        parentId: '1',
        groupCode: groupCode
    },"POST");
};
